import React from 'react';

const Club = (props) => {
  const item = props.club.members.map((member, index) => (
    <li key={index}>{member}</li>
  ));
  return (
    <article>
      <h2>{props.club.name}</h2>
      <i className={props.club.fa}></i>
      <h4>Members:</h4>
      <ul>{item}</ul>
    </article>
  );
};

export default Club;
